.quiz-wrap {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: stretch;
    overflow: hidden;

    .menu {
        display: none;

        svg {
            position: absolute;
            top: 21px;
            right: 21px;
            transition: 0.3s; } }


    .quiz {
        width: 70vw;
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 6% 2% 6% 4%;
        background-color: $gray-light;
        overflow: hidden;
        overflow-y: auto;



        .question {
            max-width: 625px; }

        .answers-wrap {
            display: flex;
            flex-wrap: wrap;
            position: relative;


            &::before {
                content: "";
                display: none;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background: transparent;
                z-index: 5; }

            &.disabled {
                &::before {
                    display: block; } } } }


    .prize-container {
        display: flex;
        padding: 10px 0;
        width: 30vw;
        min-width: 376px;
        overflow: hidden;
        overflow-y: auto;
        background: $white; }



    .prize-wrap {
        width: 100%;
        display: flex;
        flex-direction: column-reverse;
        justify-content: center; } }

@media only screen and (max-width: 992px) {
    .quiz-wrap {

        .quiz {
            width: 100%;
            padding: 136px 0 32px 0;
            align-items: center;

            .question {
                text-align: center;
                padding-left: 10px;
                padding-right: 10px;
                margin-top: 0; }

            .answers-wrap {
                justify-content: center; } }


        .prize-container {
            // display: none
            width: 100%;
            min-width: 100%;
            // justify-content: center
            // align-items: center
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            // left: 100%
            transition: 0.3s;
            transform: translateX(100%);
            z-index: 99;

            &.open {
                transform: translateX(0); } }



        .menu {
            display: block;
            z-index: 100;

            svg {
                &:last-child {
                    opacity: 0; } }

            &.open {
                svg {
                    &:first-child {
                        opacity: 0;
                        animation-name: rotateIn;
                        animation-duration: 0.3s; }

                    &:last-child {
                        animation-name: rotateBack;
                        animation-duration: 0.7s;
                        opacity: 1;
                        top: 22px; } } }

            &.close {
                svg {
                    &:first-child {
                        animation-name: rotateBack;
                        animation-duration: 0.7s; }

                    &:last-child {
                        animation-name: rotateIn;
                        animation-duration: 0.3s; } } } } }



    @keyframes rotateIn {
        0% {
            transform: rotate(90deg);
            opacity: 1; }
        100% {
            transform: rotate(0deg);
            opacity: 0; } }

    @keyframes rotateBack {
        0% {
            transform: rotate(90deg);
            opacity: 0; }
        100% {
            transform: rotate(0deg);
            opacity: 1; } } }
